.wrapper {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-auto-rows: 100px;
  grid-gap: 10px;
}
.wrapper .gallery-image:nth-child(4n + 2) {
  grid-column-end: span 2;
  grid-row-end: span 2;
  background-color: #ffa94d;
}
.wrapper .gallery-image:nth-child(2) {
  grid-column: 3;
  grid-row: 2 / 4;
}
.wrapper .gallery-image:nth-child(5) {
  grid-column: 1 / 3;
  grid-row: 1 / 3;
}
